import actionTypes from '../consts/actionTypes';
import createReducer from './createReducer';

const reducers = {};

const defaultState = {

};

reducers[actionTypes.admin.LOGGING] = state => ({
  ...state,
  logging: true,
});

reducers[actionTypes.admin.INITIALIZE_ADMIN] = (state, { admin }) => ({
  ...state,
  logging: false,
  admin
});


export default createReducer(reducers, defaultState);
